          .bloque-texto-g__texto.p-4
            p El dibujo es una forma expresin humana y una de las ms efectivas, ya que bien ejecutado es capaz de superar incluso la barrera del idioma, tanto as que algunos se han constituido en smbolos que son mundialmente utilizados para representar conceptos especficos; por ejemplo, la seal de prohibido o las figuras de hombre y mujer que se ponen en la entrada de los baos pblicos.
            br
            br
            p.mb-0 En su definicin ms bsica, el dibujo es el arte de representar objetos mediante el uso de lneas sobre una superficie, y es una de las formas ms sencillas de representacin grfica.

    p.mb-5(data-aos="flip-up") Actualmente, las tcnicas de dibujo se dividen en dos grandes ramas definidas principalmente por la naturaleza de las herramientas usadas y estas son las tcnicas anlogas y las digitales. Las tcnicas anlogas hacen referencia a todas aquellas que hacen uso de instrumentos o herramientas tradicionales como lo son el lpiz y el papel principalmente, aunque no se restringen solo a estos 2, mientras que las digitales hacen uso de computadores y una amplia gama de herramientas tanto de #[i hardware] como de #[i software].

    .row.align-items-center.bg-color1.p-5(data-aos="slide-right")
      .col-lg-12.mb-4.mb-lg-0
        figure.mb-5
          img.img-a(src='@/assets/curso/tema_1/img_2.png', alt='Texto que describa la imagen')

        p.mb-0 A su vez dentro de estas 2 ramas existen un gran abanico de categoras, las cuales se definen principalmente por el tipo de materiales que se emplean para realizarlas; por ejemplo, tcnicas secas o aguadas dentro de las anlogas o tcnicas vectoriales o mapas de bits para el caso de las digitales. Sin embargo, al ser la intencin de este documento hacer una introduccin bsica se abordarn una serie de conceptos tiles para realizar una representacin grfica de ideas simple y efectiva orientada a la realizacin de #[i storyboard].

    separador
    .titulo-segundo(data-aos="flip-up")
      #t_1_1.h2 1.1 Herramientas de dibujo

    p.mb-5(data-aos="flip-up") Para dibujar existen muchos instrumentos, utensilios y herramientas, a continuacin, se da una breve descripcin de algunos de los ms utilizados clasificados segn su uso:

    .row.align-items-center.bg-color2.p-5(data-aos="slide-left")
      .col-lg-12.mb-4.mb-lg-0
        figure.mb-5
          img.img-a(src='@/assets/curso/tema_1/img_5.png', alt='Texto que describa la imagen')

    .row.justify-content-center.mb-5(data-aos="slide-right")
      .col-lg-12
        LineaTiempoD.color-secundario
          .row.color_lin(numero="A" titulo="Lpices y portaminas")
            p El lpiz es un instrumento para dibujo o escritura que consiste habitualmente en una barra (conocida tambin como mina) compuesta de una mezcla de grafito y arcilla o cera encapsulada en un cilindro de madera (actualmente se usan muchos otros materiales como plstico, papel, etc.) que le da soporte a la mina, tambin son fabricados de colores mediante el uso de diferentes pigmentos, pero para dibujo el ms usado suele ser el lpiz de grafito.

            p.mb-5 Los lpices de grafito se marcan con letras y nmeros segn la composicin de la mina usando la H (del ingls Hard) para las minas duras y la B (del ingls Back) para las minas blandas, entre ms dura sea la mina ms claro ser el trazo dejado por el lpiz y en contraposicin, entre ms blanda sea la mina ms oscuro ser el trazo debido a que as el lpiz deja sobre el soporte mayor cantidad de grafito.

            figure.mb-5
              img.img-a(src='@/assets/curso/tema_1/img_6.png', alt='Texto que describa la imagen')

            p El portaminas es otro instrumento de dibujo anlogo al lpiz con la diferencia de que tiene un sistema mecnico para proveer las minas, las cuales se pueden reemplazar una vez se acaban.
          .row(numero="B" titulo="Bolgrafos y plumas")
            p.mb-5 Son instrumentos de escritura que cuentan con sistemas mecnicos para dosificar tinta. Aunque principalmente son usados para escribir es comn que algunos artistas los usen para dibujar.

            figure
              img.img-a(src='@/assets/curso/tema_1/img_7.png', alt='Texto que describa la imagen')
          .row(numero="C" titulo="Sacapuntas y bistur")
            p.mb-5 Los sacapuntas son utensilios usados para desbastar el recubrimiento de madera de los lpices a medida que se va desgastando la mina, existen muchos modelos de sacapuntas, pero su finalidad es la misma, tambin es comn hacer uso de cuchillas como los bisturi para hacer el desbaste de la madera y afilar las puntas de las minas a medida que se desgastan.
            figure
              img.img-a(src='@/assets/curso/tema_1/img_8.png', alt='Texto que describa la imagen')
          .row(numero="D" titulo="Papeles")
            p El papel es un material obtenido del procesamiento de fibras vegetales de las que se obtiene pulpa de celulosa mediante mtodos de molienda, la cual es procesada y prensada para obtener lminas delgadas de caractersticas variables segn el proceso que se les aplique.
            p.mb-5 Existen diferentes tipos de papel destinados a variedad de aplicaciones, aunque en principio todos sirven para dibujar, entre los ms comunes y fciles de conseguir se encuentra el papel #[i bond] de 75 gramos que regularmente es de uso de oficina, sin embargo, en casi cualquier papelera o tienda de implementos de arte se consiguen ms variedades.
            figure
              img.img-a(src='@/assets/curso/tema_1/img_9.png', alt='Texto que describa la imagen')
          .row(numero="E" titulo="Gomas de borrar")
            p.mb-5 Son instrumentos diseados para eliminar trazos de lpiz y de algunas tintas borrables, hay de consistencia rgida, aunque con algo de flexibilidad y algunas maleables como la goma limpia tipos.
            figure
