    h3(data-aos="flip-up") #[i Hardware]
    p.mb-5(data-aos="flip-up") Actualmente existen muchas alternativas de equipos de cmputo y perifricos tiles para desarrollar ilustraciones que van desde los telfonos mviles, hasta computadores de escritorio pasando por computadores y porttiles, #[i tablets] y otros perifricos especializados en la ilustracin digital 2D y 3D.

    .row.justify-content-center.mb-5(data-aos="slide-right")
      .col-lg-12
        LineaTiempoD.color-secundario
          .row(numero="A" titulo="Telfonos mviles")
            p.mb-5 Actualmente la capacidad de cmputo de los telfonos mviles supera con creces la poseda por los computadores de escritorio de finales de los 90, especialmente los que encajan en la categora de #[i Smartphone], ya que en su mayora poseen una pantalla tctil de suficiente tamao que brinda la posibilidad de dibujar sobre ella como si se tratara de una libreta de bolsillo digital, gracias a esta caracterstica y las diferentes aplicaciones y algunos utensilios como los stylus (este ltimo no es indispensable) los #[i smartphones] se convierten en una alternativa al alcance de la mayora de personas para realizar dibujos digitales. 

            figure
              img.img-a(src='@/assets/curso/tema_1/img_12.png', alt='Texto que describa la imagen')
          .row(numero="B" titulo="<em>Tablets</em>")
            p Son dispositivos con grandes pantallas tctiles, habitualmente mayores a 7 y que funcionan generalmente bajo los mismos sistemas operativos que los #[i Smartphones], como lo son android, iOs y en algunos casos Windows, gracias a esto la mayora de aplicaciones disponibles para telfonos mviles estn disponibles tambin para las #[i tablets] y dado su tamao de pantalla son una alternativa ms cmoda para dibujar que un #[i smartphone].

            p.mb-5 El dispositivo ms usado de esta categora por artistas dedicados a la ilustracin sin lugar a dudas es el iPad de Apple, sin embargo, en el mercado existen muchas otras alternativas con funcionalidades iguales o similares en un amplio rango de precios.

            figure
              img.img-a(src='@/assets/curso/tema_1/img_13.png', alt='Texto que describa la imagen')

          .row(numero="C" titulo="Computadores")
            p Los computadores son la alternativa ms utilizada en cuanto a trabajo profesional se refiere, principalmente por las posibilidades de personalizacin segn las necesidades y porque ofrecen ms capacidad de cmputo que alternativas como mviles o #[i tablets] y por tanto son capaces de ejecutar aplicaciones ms potentes para ilustracin.

            p.mb-5 Existen en 2 grandes categoras que son equipos de escritorio y equipos porttiles diferencindose principalmente por su volumen y peso lo que hace que el equipo deba ser usado en una estacin fija para el caso de los equipos de escritorio o tenga la posibilidad de ser transportado con libertad para el caso de los porttiles (conocidos tambin como #[i laptops]).

            figure
              img.img-a(src='@/assets/curso/tema_1/img_14.png', alt='Texto que describa la imagen')

          .row(numero="D" titulo="Tabletas grficas")
            p Las tabletas grficas son un perifrico de entrada que se conecta a un computador de escritorio o porttil, e incluso algunos modelos actuales permiten conexin con #[i smartphones] compatibles, constan de una superficie plana sobre la cual se interacta por medio de un lpiz que es capaz de detectar la posicin, contacto, presin y en ocasiones inclinacin para transferir esa informacin al trazo representado en pantalla.

            p.mb-5 Las hay de muchas marcas, modelos y precios siendo los fabricantes especializados ms conocidos Wacom, Xp Pen y Huion, existen otros fabricantes, pero por presencia en el mercado los modelos de estos tres fabricantes son los ms sencillos de encontrar en la regin de Latinoamrica.

            figure
              img.img-a(src='@/assets/curso/tema_1/img_15.png', alt='Texto que describa la imagen')
          .row(numero="E" titulo="Pantallas grficas")
            p En esencia tienen el mismo funcionamiento que una tableta grfica, salvo que la superficie sobre la cual se interacta con el lpiz funciona tambin como monitor, razn por la cual la experiencia de uso es ms natural, pero en contraposicin su costo es ms elevado.

            p.mb-5 Al igual que con las tabletas grficas, las marcas de dispositivos ms fciles de encontrar en la regin son Wacom, Xp Pen y Huion.

            figure
              img.img-a(src='@/assets/curso/tema_1/img_16.png', alt='Texto que describa la imagen')

    h3(data-aos="flip-up") #[i Software]

    p(data-aos="flip-up") El #[i software] es el conjunto de instrucciones (Programas) que permiten la utilizacin de los equipos de cmputo, el principal es el sistema operativo el cual cumple la funcin de ser base para la operacin del equipo y de otros programas. Las plataformas ms populares actualmente para ordenadores de escritorio y porttiles son Windows, macOS y Linux en sus diversas distribuciones y iOS y Android para dispositivos mviles, lo que ocasiona que la mayora de aplicaciones para dibujo se centren en estos sistemas operativos.

