        br
        br
        p.mb-0 Aunque el boceto se caracteriza por ser una construccin rpida es importante tener presentes algunos conceptos que se describirn a continuacin, ya que son elementos bsicos del dibujo y son importantes para hacer del proceso de bocetacin algo eficiente.

