            .tarjeta-flip__contenido.p-4.p-xl-5
              h5 Forma
              p.mb-0 Es el elemento compositivo ms pequeo de una imagen y su apariencia geomtrica se asume como algo redondo sin embargo al depender en gran medida del instrumento con el que se marca, puede obtener otras formas como el cuadrado (en el caso de los pxeles) el tringulo, el rectngulo, etc., segn la forma del instrumento de dibujo.

